import {useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
import useBnbmFinance from './useBnbmFinance';
import useRefresh from './useRefresh';

const useTotalStakedOnHeaven = () => {
  const [totalStaked, setTotalStaked] = useState(BigNumber.from(0));
  const bnbmFinance = useBnbmFinance();
  const {slowRefresh} = useRefresh();
  const isUnlocked = bnbmFinance?.isUnlocked;

  useEffect(() => {
    async function fetchTotalStaked() {
      try {
        setTotalStaked(await bnbmFinance.getTotalStakedInHeaven());
      } catch (err) {
        console.error(err);
      }
    }
    if (isUnlocked) {
      fetchTotalStaked();
    }
  }, [isUnlocked, slowRefresh, bnbmFinance]);

  return totalStaked;
};

export default useTotalStakedOnHeaven;
