import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import {TokenStat} from '../bnbm-finance/types';
import useRefresh from './useRefresh';

const useCashPriceInEstimatedTWAP = () => {
  const [stat, setStat] = useState<TokenStat>();
  const bnbmFinance = useBnbmFinance();
  const {slowRefresh} = useRefresh();

  useEffect(() => {
    async function fetchCashPrice() {
      try {
        setStat(await bnbmFinance.getBnbmStatInEstimatedTWAP());
      } catch (err) {
        console.error(err);
      }
    }
    fetchCashPrice();
  }, [setStat, bnbmFinance, slowRefresh]);

  return stat;
};

export default useCashPriceInEstimatedTWAP;
