import {useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
import useBnbmFinance from './useBnbmFinance';
import useRefresh from './useRefresh';

const useEarningsOnHeaven = () => {
  const {slowRefresh} = useRefresh();
  const [balance, setBalance] = useState(BigNumber.from(0));
  const bnbmFinance = useBnbmFinance();
  const isUnlocked = bnbmFinance?.isUnlocked;

  useEffect(() => {
    async function fetchBalance() {
      try {
        setBalance(await bnbmFinance.getEarningsOnHeaven());
      } catch (e) {
        console.error(e);
      }
    }
    if (isUnlocked) {
      fetchBalance();
    }
  }, [isUnlocked, bnbmFinance, slowRefresh]);

  return balance;
};

export default useEarningsOnHeaven;
