import {useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
import useBnbmFinance from './useBnbmFinance';
import useRefresh from './useRefresh';

const useStakedBalanceOnHeaven = () => {
  const {slowRefresh} = useRefresh();
  const [balance, setBalance] = useState(BigNumber.from(0));
  const bnbmFinance = useBnbmFinance();
  const isUnlocked = bnbmFinance?.isUnlocked;
  useEffect(() => {
    async function fetchBalance() {
      try {
        setBalance(await bnbmFinance.getStakedSharesOnHeaven());
      } catch (e) {
        console.error(e);
      }
    }
    if (isUnlocked) {
      fetchBalance();
    }
  }, [slowRefresh, isUnlocked, bnbmFinance]);
  return balance;
};

export default useStakedBalanceOnHeaven;
