import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import {AllocationTime} from '../bnbm-finance/types';
import useRefresh from './useRefresh';

const useTreasuryAllocationTimes = () => {
  const {slowRefresh} = useRefresh();
  const [time, setTime] = useState<AllocationTime>({
    from: new Date(),
    to: new Date(),
  });
  const bnbmFinance = useBnbmFinance();
  useEffect(() => {
    if (bnbmFinance) {
      bnbmFinance.getTreasuryNextAllocationTime().then(setTime);
    }
  }, [bnbmFinance, slowRefresh]);
  return time;
};

export default useTreasuryAllocationTimes;
