import {useCallback} from 'react';
import useBnbmFinance from './useBnbmFinance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';

const useRedeemOnHeaven = (description?: string) => {
  const bnbmFinance = useBnbmFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleRedeem = useCallback(() => {
    const alertDesc = description || 'Redeem SBNB from Heaven';
    handleTransactionReceipt(bnbmFinance.exitFromHeaven(), alertDesc);
  }, [bnbmFinance, description, handleTransactionReceipt]);
  return {onRedeem: handleRedeem};
};

export default useRedeemOnHeaven;
