import {useEffect, useState} from 'react';
import useBnbmFinance from '../useBnbmFinance';
import {AllocationTime} from '../../bnbm-finance/types';

const useClaimRewardTimerHeaven = () => {
  const [time, setTime] = useState<AllocationTime>({
    from: new Date(),
    to: new Date(),
  });
  const bnbmFinance = useBnbmFinance();

  useEffect(() => {
    if (bnbmFinance) {
      bnbmFinance.getUserClaimRewardTime().then(setTime);
    }
  }, [bnbmFinance]);
  return time;
};

export default useClaimRewardTimerHeaven;
