import {useCallback} from 'react';
import useBnbmFinance from './useBnbmFinance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';

const useStakeToHeaven = () => {
  const bnbmFinance = useBnbmFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleStake = useCallback(
    (amount: string) => {
      handleTransactionReceipt(bnbmFinance.stakeShareToHeaven(amount), `Stake ${amount} SBNB to the heaven`);
    },
    [bnbmFinance, handleTransactionReceipt],
  );
  return {onStake: handleStake};
};

export default useStakeToHeaven;
