import {useEffect, useState} from 'react';
import useRefresh from '../useRefresh';
import useBnbmFinance from '../useBnbmFinance';

const useClaimRewardCheck = () => {
  const {slowRefresh} = useRefresh();
  const [canClaimReward, setCanClaimReward] = useState(false);
  const bnbmFinance = useBnbmFinance();
  const isUnlocked = bnbmFinance?.isUnlocked;

  useEffect(() => {
    async function canUserClaimReward() {
      try {
        setCanClaimReward(await bnbmFinance.canUserClaimRewardFromHeaven());
      } catch (err) {
        console.error(err);
      }
    }
    if (isUnlocked) {
      canUserClaimReward();
    }
  }, [isUnlocked, slowRefresh, bnbmFinance]);

  return canClaimReward;
};

export default useClaimRewardCheck;
