import {useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import useRefresh from './useRefresh';

const useFetchHeavenAPR = () => {
  const [apr, setApr] = useState<number>(0);
  const bnbmFinance = useBnbmFinance();
  const {slowRefresh} = useRefresh();

  useEffect(() => {
    async function fetchHeavenAPR() {
      try {
        setApr(await bnbmFinance.getHeavenAPR());
      } catch (err) {
        console.error(err);
      }
    }
    fetchHeavenAPR();
  }, [setApr, bnbmFinance, slowRefresh]);

  return apr;
};

export default useFetchHeavenAPR;
