import {useCallback} from 'react';
import useBnbmFinance from './useBnbmFinance';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';

const useWithdrawFromHeaven = () => {
  const bnbmFinance = useBnbmFinance();
  const handleTransactionReceipt = useHandleTransactionReceipt();

  const handleWithdraw = useCallback(
    (amount: string) => {
      handleTransactionReceipt(
        bnbmFinance.withdrawShareFromHeaven(amount),
        `Withdraw ${amount} SBNB from the heaven`,
      );
    },
    [bnbmFinance, handleTransactionReceipt],
  );
  return {onWithdraw: handleWithdraw};
};

export default useWithdrawFromHeaven;
