import {useEffect, useState} from 'react';
import useBnbmFinance from '../useBnbmFinance';
import useRefresh from '../useRefresh';

const useWithdrawCheck = () => {
  const [canWithdraw, setCanWithdraw] = useState(false);
  const bnbmFinance = useBnbmFinance();
  const {slowRefresh} = useRefresh();
  const isUnlocked = bnbmFinance?.isUnlocked;

  useEffect(() => {
    async function canUserWithdraw() {
      try {
        setCanWithdraw(await bnbmFinance.canUserUnstakeFromHeaven());
      } catch (err) {
        console.error(err);
      }
    }
    if (isUnlocked) {
      canUserWithdraw();
    }
  }, [isUnlocked, bnbmFinance, slowRefresh]);

  return canWithdraw;
};

export default useWithdrawCheck;
