import React, {useMemo} from 'react';
import styled from 'styled-components';

import {Box, Button, Card, CardContent, Typography} from '@material-ui/core';

// import Button from '../../../components/Button';
// import Card from '../../../components/Card';
// import CardContent from '../../../components/CardContent';
import CardIcon from '../../../components/CardIcon';
import {AddIcon, RemoveIcon} from '../../../components/icons';
import IconButton from '../../../components/IconButton';
import Label from '../../../components/Label';
import Value from '../../../components/Value';

import useApprove, {ApprovalState} from '../../../hooks/useApprove';
import useModal from '../../../hooks/useModal';
import useTokenBalance from '../../../hooks/useTokenBalance';
import useWithdrawCheck from '../../../hooks/heaven/useWithdrawCheck';

import {getDisplayBalance} from '../../../utils/formatBalance';

import DepositModal from './DepositModal';
import WithdrawModal from './WithdrawModal';
import useBnbmFinance from '../../../hooks/useBnbmFinance';
import ProgressCountdown from './ProgressCountdown';
import useStakedBalanceOnHeaven from '../../../hooks/useStakedBalanceOnHeaven';
import useStakedTokenPriceInDollars from '../../../hooks/useStakedTokenPriceInDollars';
import useUnstakeTimerHeaven from '../../../hooks/heaven/useUnstakeTimerHeaven';
import TokenSymbol from '../../../components/TokenSymbol';
import useStakeToHeaven from '../../../hooks/useStakeToHeaven';
import useWithdrawFromHeaven from '../../../hooks/useWithdrawFromHeaven';

const Stake: React.FC = () => {
  const bnbmFinance = useBnbmFinance();
  const [approveStatus, approve] = useApprove(bnbmFinance.SBNB, bnbmFinance.contracts.Heaven.address);

  const tokenBalance = useTokenBalance(bnbmFinance.SBNB);
  const stakedBalance = useStakedBalanceOnHeaven();
  const {from, to} = useUnstakeTimerHeaven();

  const stakedTokenPriceInDollars = useStakedTokenPriceInDollars('SBNB', bnbmFinance.SBNB);
  const tokenPriceInDollars = useMemo(
    () =>
      stakedTokenPriceInDollars
        ? (Number(stakedTokenPriceInDollars) * Number(getDisplayBalance(stakedBalance))).toFixed(2).toString()
        : null,
    [stakedTokenPriceInDollars, stakedBalance],
  );
  // const isOldHeavenMember = heavenVersion !== 'latest';

  const {onStake} = useStakeToHeaven();
  const {onWithdraw} = useWithdrawFromHeaven();
  const canWithdrawFromHeaven = useWithdrawCheck();

  const [onPresentDeposit, onDismissDeposit] = useModal(
    <DepositModal
      max={tokenBalance}
      onConfirm={(value) => {
        onStake(value);
        onDismissDeposit();
      }}
      tokenName={'SBnb'}
    />,
  );

  const [onPresentWithdraw, onDismissWithdraw] = useModal(
    <WithdrawModal
      max={stakedBalance}
      onConfirm={(value) => {
        onWithdraw(value);
        onDismissWithdraw();
      }}
      tokenName={'SBnb'}
    />,
  );

  return (
    <Box>
      <Card>
        <CardContent>
          <StyledCardContentInner>
            <StyledCardHeader>
              <CardIcon>
                <TokenSymbol symbol="SBNB" />
              </CardIcon>
              <Value value={getDisplayBalance(stakedBalance)} />
              <Label text={`≈ $${tokenPriceInDollars}`} variant="blue" />
              <Label text={'SBNB Staked'} variant="blue" />
            </StyledCardHeader>
            <StyledCardActions>
              {approveStatus !== ApprovalState.APPROVED ? (
                <Button
                  //disabled={approveStatus !== ApprovalState.NOT_APPROVED}
                  disabled
                  color="primary" variant="contained"
                  style={{marginTop: '20px'}}
                  onClick={approve}
                >
                  Approve SBNB
                </Button>
              ) : (
                <>
                  <IconButton disabled={!canWithdrawFromHeaven} onClick={onPresentWithdraw}>
                    <RemoveIcon color={!canWithdrawFromHeaven ? '' : 'yellow'} />
                  </IconButton>
                  <StyledActionSpacer />
                  <IconButton onClick={onPresentDeposit}>
                    <AddIcon color={!canWithdrawFromHeaven ? '' : 'yellow'} />
                  </IconButton>
                </>
              )}
            </StyledCardActions>
          </StyledCardContentInner>
        </CardContent>
      </Card>
      <Box mt={2} style={{color: '#FFF'}}>
        {canWithdrawFromHeaven ? (
          ''
        ) : (
          <Card>
            <CardContent>
              <p style={{textAlign: 'center'}}>Withdraw possible in</p>
              <ProgressCountdown hideBar={true} base={from} deadline={to} description="Withdraw available in" />
            </CardContent>
          </Card>
        )}
      </Box>
    </Box>
  );
};

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`;
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 28px;
  width: 100%;
`;

const StyledActionSpacer = styled.div`
  height: ${(props) => props.theme.spacing[4]}px;
  width: ${(props) => props.theme.spacing[4]}px;
`;

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`;

export default Stake;
